<template>
  <div>
    <cms-breadcrumb to="/memberGrade" @click="rest">
      <li>{{$route.name}}</li>
    </cms-breadcrumb>
    <div class="cms-form-layout">
      <cms-prompt collapse='true'>
        <li>带 * 为必填</li>
        <li>加入会员等级权益必须进行权益设置</li>
      </cms-prompt>
      <el-form ref="levelData" :rules="levelRules" :model="levelData" label-width="210px">
        <el-form-item label="会员等级名称" prop="levelName" :error='errorMessage.levelName'>
          <el-input maxlength="20" v-model="levelData.levelName" :autofocus="true" ref="first"></el-input>
        </el-form-item>
        <el-form-item label="等级升级条件" prop="levelExp" :error='errorMessage.levelExp'>
          <el-input maxlength="10" v-model="levelData.levelExp"></el-input>
        </el-form-item>
        <el-form-item class="member-grade" label="会员等级logo" prop="logoResourceId" :error='errorMessage.logoResourceId'>
          <cms-picture-zone v-model="levelData.logoResourceId"></cms-picture-zone>
        </el-form-item>
      </el-form>
      <h3 class="cms-segmentation">会员等级权益</h3>
      <div class="grade-items">
        <div class="grade-item">
          <el-row>
            <el-col :span="10">
              <div class="grade-img">
                <img src="https://img12.360buyimg.com/n7/jfs/t17989/208/1590957743/451183/f4572fbf/5ad05c50N3acc4ab3.jpg">
              </div>
            </el-col>
            <el-col :span="14">
              <span class="grade-item-title">生日特权</span><br />
              <span style="font-size:14px;">生日享有系统赠送的奖励</span>
            </el-col>
          </el-row>
          <el-row class="grade-item-cont">
            <el-col :span="24">
              <span v-if="this.isSetRight">
                <span v-if="rightData.rewardType<=2">
                  <span v-if="rightData.sendStyle==1">可固定获得{{rightData.maxValue}}个
                    <span v-if="rightData.rewardType==1">成长值</span>
                    <span v-if="rightData.rewardType==2">积分</span>
                  </span>
                  <span v-if="rightData.sendStyle==2">可随机获得{{rightData.minValue}}-{{rightData.maxValue}}个
                    <span v-if="rightData.rewardType==1">成长值</span>
                    <span v-if="rightData.rewardType==2">积分</span>
                  </span>
                </span>
                <span v-if="rightData.rewardType==3">
                  可获得一张（{{couponName}}）优惠券
                </span><br>
              </span>
              <span>仅限生日当天使用（当天23:59:59）</span>
            </el-col>
          </el-row>
          <el-row class="grade-item-foot">
            <el-col :span="24">
              <el-checkbox style="margin:0 15px;" v-model="isSetRight"></el-checkbox>
              <el-button size="small" @click="interestsDialog = true">权益设置</el-button>
              <el-button size="small" @click="rest">清空设置</el-button>
            </el-col>
          </el-row>
        </div>
      </div>
      <div class="cms-form-footer">
        <cms-button @click="save(true)" v-color>保存</cms-button>
      </div>
      <!-- 弹出框 -->
      <el-dialog title="权益设置" :visible.sync="interestsDialog" width="530px" :before-close="handleClose">
        <el-form ref="rightData" :rules="rightRules" :model="rightData" label-width="140px">
          <el-form-item label="权益奖励类型:" prop="rewardType" :error='errorMessage.rewardType'>
            <el-radio-group v-model="rightData.rewardType" @change="tabChange">
              <el-radio :label="1">成长值</el-radio>
              <el-radio :label="2">积分</el-radio>
              <el-radio :label="3">优惠券</el-radio>
            </el-radio-group>
            <div class="detail">优惠券额度为全平台通用</div>
          </el-form-item>
          <el-form-item label="派发方式:" prop="sendwayType" :error='errorMessage.sendwayType'>
            <el-radio-group v-model="rightData.sendwayType">
              <el-radio :label="1">系统自为</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item ref="fix" v-if="rightData.rewardType<=2" class="send-style"
           label="奖励额度:" prop="maxValue"
            :rules="rightData.sendStyle==1 ? [
              { required: true, message: '此项必填' ,trigger: 'blur'},
              { type: 'number', message: '数字类型' ,trigger: 'blur'},
             ] :[] ">
            <div style="margin-bottom:5px;">
              <el-radio v-model="rightData.sendStyle" :label="1">固定</el-radio>
              <el-input v-model.number="rightData.maxValue" v-if="rightData.sendStyle==1" maxlength="8"
               style="width:50%;margin-left:10px;">
              </el-input>
            </div>
            <div>
              <el-radio style="vertical-align: middle;position:relative;bottom:8px;"
               @change="changeValue" v-model="rightData.sendStyle" :label="2">随机</el-radio>
              <div style="display:inline-block;width:300px;" class="send-style2" v-if="rightData.sendStyle==2">
                <el-col :span="11">
                  <el-form-item style="padding:0" prop="minValue">
                    <el-input style="width:90%" v-model="rightData.minValue" maxlength="8" >
                    </el-input>
                  </el-form-item>
                </el-col>
                <el-col class="line" :span="1">-
                </el-col>
                <el-col :span="11">
                  <el-form-item style="padding:0"  prop="maxValue">
                    <el-input  style="width:90%" v-model="rightData.maxValue" maxlength="8"></el-input>
                  </el-form-item>
                </el-col>
              </div>
            </div>
          </el-form-item>
          <el-form-item class="grade-coupon" v-if="rightData.rewardType==3" label="优惠券:"
          prop="couponId" :error='errorMessage.couponId'>
              <el-select @change="getCouponName" style="width:200px;" v-model="rightData.couponId">
                <el-option v-for="item in rightData.couponList" :key="item.id"
                :label="item.name" :value="item.id"></el-option>
              </el-select>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="validateCancel">取 消</el-button>
          <el-button type="primary" @click="validateRightData">确 定</el-button>
        </span>
      </el-dialog>
    </div>
  </div>
</template>
<script>
import rules from '@/rules'
import form from '@/mixins/form.js'
import { memberlevelAdd, checkLevelName } from '@/api/users/level.js'
import { couponPage, couponGet } from '@/api/marketing/index.js'
var memberLevel = {
  logoResourceId: '',
  levelName: '',
  levelExp: 10
}
var rightList = {
  type: 2,
  rewardType: 1,
  sendwayType: 1,
  minValue: 10,
  maxValue: 20,
  sendStyle: 1,
  couponId: '',
  couponList: []
}
export default {
  mixins: [form],
  data() {
    var isLevelName = (rule, value, callback) => {
      // 验证等级会员名称
      checkLevelName({ levelName: value }).then(res => {
        if (res.code === 200) {
          if (res.data) {
            callback()
          } else {
            callback(new Error('等级会员名称已存在，不允许重复添加'))
          }
        }
      })
    }
    var isAmount = (rule, value, callback) => {
      if (Number(value) > Number(this.rightData.minValue)) {
        callback()
      } else {
        callback(new Error('最小金额要小于最大金额'))
      }
    }
    return {
      interestsDialog: false,
      levelData: Object.assign({}, memberLevel), // 等级列表数据
      rightData: Object.assign({}, rightList), // 权益列表数据
      isSetRight: false, // 是否设置权益按钮的复选框
      levelRules: {// 等级列表验证规则
        levelName: [rules.required(), rules.custom(isLevelName)],
        levelExp: [rules.required(), rules.number()],
        logoResourceId: [rules.required('必须上传一个标识', 'change')]
      },
      rightRules: {// 权益列表验证规则
        typeCode: [rules.required()],
        sendwayType: [rules.required()],
        rewardType: [rules.required()],
        minValue: [rules.required(), rules.number()],
        maxValue: [rules.required(), rules.number(), rules.custom(isAmount)],
        couponId: [rules.required('此项必填', 'change')]
      },
      couponName: '' // 权益的优惠券名称
    }
  },
  watch: {
    'rightData': {// 自动填充最大值
      handler(newValue, oldValue) {
        if (newValue.sendStyle === 1) {
          newValue.minValue = newValue.maxValue
        }
      },
      deep: true
    }
  },
  methods: {
    getCouponList() { // 获取优惠券列表
      couponPage().then(res => {
        if (res.code === 200) {
          this.rightData.couponList = res.data.content
        } else {
          this.$errorMessage(res.message)
        }
      })
    },
    save(state) { // 新增
      this.$refs['levelData'].validate((valid) => {
        if (valid) {
          let params = {}
          if (!this.isSetRight) { // 若不设置权益(则发送等级名称)
            params = { memberLevel: this.levelData }
          } else {
            const rightArr = []// 构造后台所需的数据格式
            delete this.rightData.sendStyle// 删除自定义的字段
            rightArr.push(this.rightData)
            if (this.rightData.rewardType === 3) {
              this.rightData.minValue = this.rightData.maxValue = 0
            }
            params = {
              memberLevel: this.levelData,
              levelRightList: rightArr
            }
          }
          memberlevelAdd(params).then(res => {
            if (res.code === 200) {
              this.$successMessage('新增成功')
              this.$routerLink('/memberGrade/list', 'list')
            } else {
              this.$errorMessage(res.message)
            }
          })
        } else {
          return false
        }
      })
    },
    validateRightData() { // 弹出框确定按钮的事件(需要验证必填项)
      this.$refs['rightData'].validate((valid) => {
        const reg = /^([1-9]\d*|[0]{1,1})$/g
        if (this.rightData.sendStyle !== 1) {
          if (valid) {
            this.interestsDialog = false
          }
        }
        if (this.rightData.sendStyle === 1) {
          if (valid) {
            if (reg.test(parseInt(this.rightData.maxValue)) && Number.isInteger(parseInt(this.rightData.maxValue))) {
              this.interestsDialog = false
            } else {
              this.$errorMessage('请输入合法的数字')
            }
          } else {
            return false
          }
        }
      })
    },
    validateCancel() { // 弹出框取消按钮
      this.interestsDialog = false
      this.$refs.rightData.resetFields()
    },
    tabChange() { // 切换权益类型重置数据
      this.rightData.maxValue = 20
      this.rightData.minValue = 10
      this.rightData.couponId = ''
      this.rightData.sendStyle = 1
    },
    changeValue() {
      this.rightData.maxValue = 20
      this.rightData.minValue = 10
      this.$refs['fix'].clearValidate()
    }, // 切换发送方式重置数据
    handleClose() { // 点击关闭按钮重置弹出框
      this.interestsDialog = false
      this.$refs.rightData.resetFields()
    },
    rest() { // 重置表单
      this.$refs.rightData.resetFields()
      this.rightData = Object.assign({}, rightList)
      this.$refs.first.focus()
      this.getCouponList()
    },
    getCouponName() { // 获取优惠券名称
      couponGet({ id: this.rightData.couponId }).then(res => {
        this.couponName = res.data.name
      })
    }
  },
  created() {
    this.getCouponList()
  }
}
</script>
<style lang="scss" scoped>
.grade-items {
  width: 100%;
  padding: 30px 0 50px 210px;
  display: flex;
}
.grade-img {
  width: 100px;
  height: 85px;
  overflow: hidden;
  img {
    max-width: 85%;
    height: auto;
  }
}
.grade-item {
  width: 260px;
  margin-right: 150px;
  line-height: 18px;
  color: #666666;
}
.grade-item-cont {
  background-color: #e5cfa0;
  color: #fff;
  font-size: 14px;
  padding: 10px;
}
.grade-item-foot {
  margin-top: 10px;
}
.grade-item-title {
  font-size: 16px;
  line-height: 30px;
  font-weight: bold;
}
.el-form-item .detail {
  color: #999999;
  font-size: 13px;
}
</style>





